var async = {
  metadata: {},
  tasks: [],
  running: false,
  run: function () {
    if (this.tasks.length===0) { 
      console.log('All tasks finished...waiting...')
      return
    }
    
    var task = this.tasks.shift()

    var chapters = task.chapters
    if (task.task === 'TRY_MOVE_FILE') {
      if (task.fromFolderId !== task.toFolderId) {
        async.tasks.push({
          task: 'STRICT_MOVE_FILE',
          fileId: task.fileId,
          fromFolderId: task.fromFolderId,
          toFolderId: task.toFolderId
        })
      }
      // craete the metadata
      this.createMetadata(chapters)
      async.tasks.push({
        task: 'UPDATE_METADATA',
        data: this.metadata
      })
      // this.metadata = metadata
      this.run()
    } else if (task.task === 'CHANGE_CHAPTER_INDEX') {
      // create the metadata
      console.log('Cteating index change metadata')
      
      this.createMetadata(chapters)

      this.tasks.push({
        task: 'UPDATE_METADATA',
        data: this.metadata
      })
      this.run()
    } else if (task.task === 'UPDATE_METADATA') {
      // updating metadata
      // console.log(task.data)
      console.log('Updating metadata')
      this.run()
    } else if (task.task === 'STRICT_MOVE_FILE') {
      this.run()
    } else if (task.task === 'STRICT_MOVE_FILE') {
      // move the file
      // call this.run()when done

      console.log('Moving file from chapter to chapter')
      this.run()
    } else if (task.task === 'UPLOAD_FILE') {
      // register task for file upload
      var data = task.data
      var fileId = task.fileId
      this.run()
    } else if (task.task === 'CREATE_CHAPTER') {
      // create metadata
      // create a chapter
      // update the metadata
      this.run()
    } else if (task.task === 'DELETE_CHAPTER') {
      // delete whole chapter with warning
      this.run()
    } else if (task.task === 'CREATE_TOPIC') {
      // create file using chapter id
      this.run()
    } else if (task.task === 'DELETE_TOPIC') {
      // delete file using fileID
      this.run()
    } else if (task.task === 'RENAME_TOPIC') {
      // create metadata
      this.run()
    } else if (task.task === 'RENAME_CHAPTER') {
      // create metadata
      this.run()
    }
  },
  createMetadata: function (chapters) {
    var metadata = {}
    var currentChapterIndex = -1
    var currentChapter = {}
    var children = []
    chapters.forEach(function(_el, _index) {
      metadata.chapters = metadata.chapters || []
      if (_el.type === 'chapter') {
        currentChapter = metadata.chapters[++currentChapterIndex] = {}
        currentChapter.id = _el.id
        currentChapter.index_id = _el.index_id
        currentChapter.name = _el.name
      } else {
        children = currentChapter.topics = currentChapter.topics || []
        children.push({
          id: _el.id,
          folderId: _el.parentId,
          name: _el.name
        })
      }
    });
    this.metadata = metadata
  }
}

export default async;


