class _Notes extends Component {
  constructor () {
    super()
    this.state = {
      editors: [],
      chapters: [
        {
          name: "chapter ",
          active: true,
          type: 'chapter',
          children: 0,
          id: '1',
          gIndex: 0
        },
        {
          name: "chapter 2",
          active: false,
          type: 'chapter',
          children: 2,
          id: '123',
          gIndex: 1
        },
        {
          name: "chapter 21",
          active: false,
          type: 'topic',
          id: '3',
          parentId: '123',
          lIndex: 2,
          gIndex: 2
        },
        {
          name: "chapter 22",
          active: false,
          type: 'topic',
          id: '4',
          parentId: '123',
          lIndex: 1,
          gIndex: 3
        },
        {
          name: "chapter 3",
          active: false,
          type: 'chapter',
          children: 2,
          id: '125',
          gIndex: 4
        },
        {
          name: "chapter 31",
          active: false,
          type: 'topic',
          id: '6',
          parentId: '125',
          lIndex: 1,
          gIndex: 5
        },
        {
          name: "chapter 32",
          active: false,
          type: 'topic',
          id: '7',
          parentId: '125',
          lIndex: 2,
          gIndex: 3
        }
        // {
        //   name: "chapter 1 lorem ipsum dollar emmet su ma ko",
        //   active: true,
        //   type: 'chapter',
        //   children: 0,
        //   id: '123456',
        // }
        // {
        //   name: "chapter 1 lorem ipsum dollar emmet su ma ko",
        //   active: false,
        //   type: 'topic',
        //   parentId: 'asdasdsad',
        //   id: '123456'
        // }
      ],
      noteEditors: [],
      activeEditor: 0
    }

    // create Note object

    // parseEditorNo(this.state.chapters, this.state.noteEditors)

  }

  activeLink (id) {
    var chapters = this.state.chapters
    for(var i = 0, length1 = chapters.length; i < length1; i++){
      if (chapters[i].id === id) {
        chapters[i].active = true;
      } else {
        chapters[i].active = false;
      }
    }
    this.setState({
      chapters: chapters
    })
  }

  onSelect (q) {
    this.setState({
      activeEditor: parseInt(q, 10)
    })
    this.activeLink(q, this.state.chapters)
  }

  componentDidMount() {
    document.onscroll = function (e) {
      console.log(e)
    }
  }

  render() {
    return (
      <div className="course-notes">
        <br/>
        <div className="row">
          <div className="col md-3 ">
            <div className="sidebar-note-content">
              <SidebarContentEditor parent={this} chapters={this.state.chapters} onSelect={this.onSelect.bind(this)}/>
            </div>
          </div>
          <div className="col md-9">
            <div className="note-reading-space">
              <CompactEditor
                parent={this}
                showEditor={false}
                onSave={()=>{}}
                onEdit={()=>{}}
                id={'course-notes-editor-' + this.state.activeEditor}
                key={this.state.activeEditor} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default _Notes;


